@font-face {
  font-family: 'Rubik';
  font-weight: 300;
  font-style: normal;
  src: url("./assets/font/Rubik-Light.woff2") format("woff2"), url("./assets/font/Rubik-Light.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: 'Rubik';
  font-weight: 400;
  font-style: normal;
  src: url("./assets/font/Rubik-Regular.woff2") format("woff2"), url("./assets/font/Rubik-Regular.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: 'Rubik';
  font-weight: 500;
  font-style: normal;
  src: url("./assets/font/Rubik-Medium.woff2") format("woff2"), url("./assets/font/Rubik-Medium.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: 'Rubik';
  font-weight: 700;
  font-style: normal;
  src: url("./assets/font/Rubik-Bold.woff2") format("woff2"), url("./assets/font/Rubik-Bold.woff") format("woff");
  font-display: swap;
}

body {
  min-height: 100vh;
  font-family: 'Rubik', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 1.3;
  letter-spacing: 0.6px;
  /* background-color: white;
  color: black; */
}

.App {
  text-align: center;
}

.container {
  max-width: 1220px;
  padding: 0 10px;
  margin: 0 auto;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip-path: (0, 0, 0, 0);
  border: 0;
}